.CardsRestaurantsContent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    .orientationRestaurants{
        display: flex;
        .cards{
            width: 100%;
            padding-left: 15px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            box-sizing: border-box;
            text-decoration: none;

            .caseNew{
                display: flex;
                float: left;
                width : 80px;
                height : 30px;
                text-align: center;
                background-color: var(#99E2D0);
                color:green;
            }
            .imgrestaurants{
                width: 100%;
                height: 115px;
                border-top-left-radius: 10px;
                border-top-right-radius: 10px;
                object-fit: cover;
            }
            h3{
                font-size: large;
                margin-top: 10px;
                margin-bottom: 4px;
            }
            .card-Subtitle{
                margin: 0;
            }
            .icone-Coeurvide{
                display: flex;
                float : left;
            }
        }
    }
}





                                             